
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.empty {
  height: 200vh;
}
.show__text {
  color: black;
  -webkit-transition: color 1s ease;
  transition: color 1s ease;
}
.show__text.active {
    color: red;
}
